<template>
  <div class="my">
	<div class="im">
		<img class="rege" src="../../public/wyyimg/rege.png" alt="">
	</div>
	<div class="song">
		<li v-for="(item,index) in songlist" :key="index" @click="go(item,songlist,index)">
			<div class="ranking">
				<span>{{index+1 < 10 ? '0' + (index+1) : index+1}}</span>
			</div>
			<div class="songname">
				<div class="name">
					<span>{{item.name}}
					<span class="episode">{{item.alia[0]}}</span>
					</span>
				</div>
				<div class="singer">
					<div class="sq">
						<img src="../../public/wyyimg/sq.png" alt="">
					</div>
					<div class="gn">
						<span class="author">
							{{item.ar[0].name}} - {{item.al.name}}
						</span>
					</div>
				</div>
			</div>
			<div class="tp">
				<div class="pofang">
					<img class="pofangs" src="../../public/wyyimg/bofang.png">
				</div>
			</div>
		</li>
	</div>	
  </div>
</template>
<script>
	import axios from 'axios'
	export default{
		name:'my',
		data(){
			return{
				songlist:null,
			}
		},
		methods:{
			go(item,songlist,index) {
				this.$router.push({name:'player',params:{a:item.al.picUrl,b:item.id,son:songlist,index:index}})
			},
		},
		created(){
			axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/top/list?idx=1')
			.then(res => {
				this.songlist = res.data.playlist.tracks.slice(0,20)
				console.log(this.songlist)
			})
			.catch(err => {
				console.log("请求失败" + err)
			})
		}
	}
</script>
<style scoped lang="less">
	.my{
		width: 100%;
		.im{
			width: 100%;
			height: 100%;
			overflow: hidden;
			.rege{
				width: 100%;
			}
		}
		.song{
			width: 100%;
			overflow: hidden;
			li{
				position: relative;
				list-style: none;
				width: 100%;
				height: 55px;
				padding-left: 10px;
				.ranking{
					width: 28px;
					height: 55px;
					line-height: 55px;
					color: #999;
					float: left;
				}
				.songname{
					width: 337px;
					height: 42px;
					padding: 6px 0;
					float: left;
					border-bottom: 1px solid #F2F2F3;
					.name{
						width: 100%;
						height: 25px;
						white-space:nowrap;
						overflow:hidden;
						text-overflow:ellipsis;
						max-width: 290px;
						.episode{
							
							color: #888888;
						}
					}
					.singer{
						width: 100%;
						height: 18px;
						color: #888888;
					}
					.sq{
						float: left;
						width: 20px;
						height: 18px;
						img{
							width: 100%;
						}
					}
					.author{
						float: left;
						font-size: 12px;
						color: #888888;
					}
				}
				.tp{
					position: absolute;
					width: 35px;
					height: 35px;
					right: 0;
					top: 0;
					padding: 10px 10px 10px 10px;
					.pofang{
						width: 25px;
						height: 25px;
						margin-top:5px;
						margin-left:5px;
						.pofangs{
							width: 100%;
						}
					}
				}
			}
		}
	}
</style>